:root{
  --p:hsl(237, 18%, 59%);
  --red:hsl(345, 95%, 68%);

  --dark-blue:hsl(236, 21%, 26%);
  --very-dark-blue:hsl(235, 16%, 14%);
  --black-blue:hsl(234, 17%, 12%);
}

*{
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

body{
  background-color: var(--black-blue);
  min-height: 100vh;
}